  font-family: 'Fredericka the Great',cursive;
  color: #333
}
.start-screen--style-2 .__title {
  line-height: .9;
  font-size: 3.5rem;
  font-weight: 700;
  letter-spacing: -.05em
}
.start-screen--style-2 .__title span {
  font-weight: 200
}
@media (min-width:576px) {
  .start-screen--style-2 .__title {
    font-size: 4.5rem
  }
  .start-screen--style-3 .start-screen__slide {
    height: 620px;
    max-height: calc(100vh - 85px)
  }
}
.start-screen--style-2 .video-block {
  padding: 25px 0;
  background-color: #fafafa;
  text-align: center
}
@media (min-width:768px) {
  .start-screen--style-2 .__title {
    font-size: 6rem
  }
  .start-screen--style-2 .start-screen__panel {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0
  }
  .start-screen--style-2 .video-block {
    width: 310px;
    margin-left: auto;
    padding: 37px 0
  }
}
.start-screen--style-3 .start-screen__slide {
  min-height: 400px
}
.start-screen--style-3 .start-screen__slide--1 .__title {
  font-weight: 400;
  font-family: 'Fredericka the Great',cursive;
  color: #333
}
.start-screen--style-3 .start-screen__slider-nav {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%)
}
@media (min-width:1300px) {
  .start-screen--style-3 .start-screen__slider-nav {
    left: 25px
  }
}
.start-screen--style-3 .__title {
  line-height: 1;
  font-size: 3rem;
  font-weight: 700;
  letter-spacing: -.05em
}
.start-screen--style-3 .__title span {
  font-weight: 200
}
@media (min-width:576px) {
  .start-screen--style-3 .__title {
    font-size: 4.5rem
  }
}
@media (min-width:768px) {
  .start-screen--style-3 .__title {
    font-size: 6rem
  }
}
@media (min-width:992px) {
  .start-screen--style-3 .__title {
    font-size: 7.5rem
  }
}
.hero {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #f95a3f;
  color: #fff;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none
}
.hero__inner {
  display: flex;
  align-items: center;
  height: 300px;
  padding-top: 30px;
  padding-bottom: 30px
}
@media (min-width:576px) {
  .hero__inner {
    height: 400px
  }
}
.hero__subtitle,
.hero__title {
  margin-top: 10px;
  margin-bottom: 10px
}
.hero__title {
  line-height: 1.1;
  color: inherit
}
.hero__subtitle {
  color: #f95a3f;
  font-family: Raleway,sans-serif;
  font-weight: 600
}
.page-shop .hero__inner {
  height: 265px
}
.page-shop .hero__subtitle {
  color: #fff
}
.page-404 .hero {
  background-position: 40% 50%
}
.page-404 .hero__inner {
  flex-direction: column;
  height: auto;
  min-height: 100vh
}
.custom-btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding-left: 28px;
  padding-right: 28px;
  line-height: 1;
  font-size: 1.6rem;
  font-family: Assistant,sans-serif;
  font-weight: 600;
  text-align: center!important;
  text-decoration: none!important;
  text-shadow: none!important;
  letter-spacing: 0;
  border: 2px solid;
  border-radius: 0;
  box-shadow: none;
  outline: 0;
  cursor: pointer;
  user-select: none;
  -webkit-user-drag: none;
  user-drag: none;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  z-index: 0;
  transition: background-color .25s ease-in-out,border-color .25s ease-in-out,color .25s ease-in-out
}
@media screen and (prefers-reduced-motion:reduce) {
  .custom-btn,
  .top-bar__action .custom-btn:first-child:before,
  .top-bar__navigation a:after {
    transition: none
  }
}
.custom-btn--medium {
  min-width: 150px;
  min-height: 50px;
  padding-top: 15px;
  padding-bottom: 15px
}
.custom-btn--big {
  min-width: 180px;
  min-height: 60px;
  padding-top: 20px;
  padding-bottom: 20px
}
.custom-btn.custom-btn--s1 {
  color: #333;
  background-color: #fff;
  border-color: #f95a3f
}
.custom-btn.custom-btn--s1:focus,
.custom-btn.custom-btn--s1:hover {
  background-color: #f95a3f;
  color: #fff
}
.custom-btn.custom-btn--s2 {
  background-color: #f95a3f;
  border-color: #f95a3f;
  color: #fff
}
.custom-btn.custom-btn--s2:focus,
.custom-btn.custom-btn--s2:hover {
  background-color: #fff;
  border-color: #fff;
  color: #333
}
.custom-btn.custom-btn--s3 {
  background-color: #f95a3f;
  border-color: #f95a3f;
  color: #fff
}
.custom-btn.custom-btn--s3:focus,
.custom-btn.custom-btn--s3:hover,
.custom-btn.custom-btn--s4 {
  background-color: #25272a;
  border-color: #25272a;
  color: #fff
}
.custom-btn.custom-btn--s4:focus,
.custom-btn.custom-btn--s4:hover,
.custom-btn.custom-btn--s5 {
  background-color: #fff;
  border-color: #fff;
  color: #333
}
.custom-btn.custom-btn--s5:focus,
.custom-btn.custom-btn--s5:hover {
  background-color: #25272a;
  border-color: #25272a;
  color: #fff
}
.custom-btn.wide {
  width: 100%
}
.site-logo {
  display: inline-block
}
.site-logo img {
  vertical-align: middle;
  max-width: 100%
}
.s-btns ul {
  margin-top: -10px;
  margin-left: -25px;
  line-height: 0;
  font-size: 0;
  letter-spacing: -1px
}
.s-btns li {
  margin-top: 10px;
  margin-left: 25px
}
.s-btns a {
  display: block;
  width: 1em;
  font-size: 2rem;
  letter-spacing: 0;
  text-align: center
}
.s-btns a i {
  vertical-align: middle;
  line-height: 1
}
.s-btns--white a:not(:hover):not(:focus) {
